import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.warehouse, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 200,
      dataIndex: 'warehouseStatus',
      key: 'warehouseStatus',
      render: (text, record, index) => <div className="tableOperation">
        <span className='blue' onClick={() => {
          this.onEdit(text, record, index)
        }}>编辑</span>
        {
          record.productInitStatus != 1 ? <span style={{marginRight: '12px'}} onClick={() => {
            this.handleInit(text, record, index)
          }}>商品初始化</span> : null
        }
        {/* {
          text ? <span onClick={() => {
            this.handleOperator(text, record, index, 0)
          }}>禁用</span> : <span onClick={() => {
            this.handleOperator(text, record, index, 1)
          }}>启用</span>
        } */}
      </div>
    }, {
      title: '仓库编号',
      width: 100,
      dataIndex: 'warehouseCode',
      key: 'warehouseCode'
    }, {
      title: '仓库名称',
      width: 200,
      dataIndex: 'warehouseName',
      key: 'warehouseName'
    }, {
      title: '仓库类型',
      width: 150,
      dataIndex: 'warehouseTypeDesc',
      key: 'warehouseTypeDesc'
    }, {
      title: '仓库用途',
      width: 100,
      dataIndex: 'warehousePurposeDesc',
      key: 'warehousePurposeDesc'
    }
    // , {
    //   title: '负库存',
    //   width: 100,
    //   dataIndex: 'negativeStockFlag',
    //   key: 'negativeStockFlag',
    //   render: (text, record) => <span className={record.negativeStockFlag == 1 ? 'green' : 'red'}>{text == 1 ? '是' : '否'}</span>
    // }
    , {
      title: '初始化状态',
      width: 100,
      dataIndex: 'productInitStatusDesc',
      key: 'productInitStatusDesc',
      render: (text, record) => <span className={record.productInitStatus == 1 ? 'green' : 'red'}>{text}</span>
    }, {
      title: '仓库容积(M3)',
      width: 150,
      dataIndex: 'warehouseVolume',
      key: 'warehouseVolume'
    }, {
      title: '仓库面积(M2)',
      width: 150,
      dataIndex: 'warehouseArea',
      key: 'warehouseArea'
    }, {
      title: '默认标准区',
      width: 150,
      dataIndex: 'defaultStandardArea',
      key: 'defaultStandardArea'
    }, {
      title: '默认退货区',
      width: 150,
      dataIndex: 'defaultReturnProductArea',
      key: 'defaultReturnProductArea'
    }, {
      title: '默认途损区',
      width: 150,
      dataIndex: 'defaultRoadDamageArea',
      key: 'defaultRoadDamageArea'
    }, {
      title: '默认账面直通区',
      width: 150,
      dataIndex: 'defaultAccountArea',
      key: 'defaultAccountArea'
    }, {
      title: '默认盘点待处理区',
      width: 150,
      dataIndex: 'defaultCheckPendingArea',
      key: 'defaultCheckPendingArea'
    }, {
      title: '修改时间',
      width: 150,
      dataIndex: 'updateTime',
      key: 'updateTime'
    }, {
      title: '修改人',
      width: 100,
      dataIndex: 'updateBy',
      key: 'updateBy'
    }]
  }

  // 初始化
  handleInit(text, record, index) {
    const {handleGetList, searchParams, page, size, handleInit} = this.props
    handleInit({
      data: record
    }, () => {
      message.success('初始化成功')
      handleGetList({searchParams: {page, size, ...searchParams}})
    })
  }

  handleOperator(text, record, index, type) {
    const {handleGetList, searchParams, page, size, handleOperator} = this.props
    confirm({
      title: '温馨提示',
      content: `是否${type ? '启用' : '禁用'}该条信息？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator({
          data: {
            id: record.id,
            warehouseStatus: type
          },
          urlParams: [record.id]
        }, () => {
          message.success('操作成功')
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  onEdit(text, record, index) {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '编辑仓库', modalData: record})
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value ) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if ( newProps.userLoation.value != userLoation.value ) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange =  (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



