import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect} from 'Form'
import {API_WAREHOUSE_WAREHOUSETYPES} from 'api'

@connect(
  state => ({...state.warehouse}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '新增仓库', modalData: ''})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {warehouseCode, warehouseName, productInitStatus}
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>仓库编号：</label>
              <div className='box'>
                <Input value={warehouseCode} onChange={(e) => {
                  this.onInputChange({warehouseCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>仓库名称：</label>
              <div className='box'>
                <Input value={warehouseName} onChange={(e) => {
                  this.onInputChange({warehouseName: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_WAREHOUSE_WAREHOUSETYPES}
              searchParams={{type: 'I'}}
              label='初始化状态'
              selectItem={{data: productInitStatus, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({productInitStatus: value})
              }}
            />
          </Col>
          <Col span={6}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" onClick={this.handleAdd}>新增</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



