import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  message,
  Button
} from 'antd'

import {CsxFormSelect} from 'Form'
import {CsxFormCascader} from 'Form'
import {API_WAREHOUSE_WAREHOUSETYPES, API_DISTRICE_LIST, API_DICTIONARYS} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.warehouse}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      console.log(modalData)
      // 特殊处理数据
      reqObj.warehouseType = {
        children: reqObj.warehouseTypeDesc && reqObj.warehouseTypeDesc.toString(),
        value: reqObj.warehouseType && reqObj.warehouseType.toString()
      }
      // reqObj.negativeStockFlag = {
      //   value: reqObj.negativeStockFlag && reqObj.negativeStockFlag.toString(),
      //   children: reqObj.negativeStockFlag && reqObj.negativeStockFlag.toString()
      // }
      reqObj.relevancePlaceCode = {
        children: reqObj.relevancePlaceName && reqObj.relevancePlaceName.toString(),
        value: reqObj.relevancePlaceCode && reqObj.relevancePlaceCode.toString()
      }
      reqObj.warehousePurpose = {
        children: reqObj.warehousePurposeDesc && reqObj.warehousePurposeDesc.toString(),
        value: reqObj.warehousePurpose && reqObj.warehousePurpose.toString()
      }
      reqObj.warehouseCode = reqObj.relevancePlaceCode.value
      // 三级联动还原
      let value = []
      let children = []
      reqObj.provinceCode && (value.push({value: reqObj.provinceCode, label: reqObj.provinceName}), children.push(reqObj.provinceCode))
      reqObj.cityCode && (value.push({value: reqObj.cityCode, label: reqObj.cityName}), children.push(reqObj.cityCode))
      reqObj.regionCode && (value.push({value: reqObj.regionCode, label: reqObj.regionName}), children.push(reqObj.regionCode))
      reqObj.threeCode = {value, children}
      update({modalData: {...reqObj}})
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleAddOrEdit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            // 特殊处理关联地点
            if (i == 'relevancePlaceCode') {
              reqObj.warehouseCode = reqObj[i].value,
                reqObj.relevancePlaceName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }
        // 特殊处理三级联动的值
        reqObj.threeCode[0] && (reqObj.provinceCode = reqObj.threeCode[0].value, reqObj.provinceName = reqObj.threeCode[0].label)
        reqObj.threeCode[1] && (reqObj.cityCode = reqObj.threeCode[1].value, reqObj.cityName = reqObj.threeCode[1].label)
        reqObj.threeCode[2] && (reqObj.regionCode = reqObj.threeCode[2].value, reqObj.regionName = reqObj.threeCode[2].label)
        delete reqObj.threeCode

        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }

  handleSelectChange = () => {
    const {form} = this.props
    let inputObj = form.getFieldsValue()
    let warehouseName = inputObj.relevancePlaceCode.children.replace(inputObj.relevancePlaceCode.value, '')
    form.setFieldsValue({
      warehouseName
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='900px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <h2>基础信息</h2>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="仓库编号"
              >
                {getFieldDecorator('relevancePlaceCode', {
                  initialValue: modalData.relevancePlaceCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_WAREHOUSE_WAREHOUSETYPES}
                    antd={{disabled: modalData && modalData.id ? true : false}}
                    searchParams={{type: 'P'}}
                    selectItem={{key: 'key', valueKey: ['key', 'value']}}
                    showValue='value'
                    handleSelectChange={this.handleSelectChange}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="仓库名称"
              >
                {getFieldDecorator('warehouseName', {
                  initialValue: modalData.warehouseName
                })(
                  // <Input disabled = { modalData.warehouseName ? true : false } />
                  <Input disabled />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="仓库类型"
              >
                {getFieldDecorator('warehouseType', {
                  initialValue: modalData.warehouseType,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_DICTIONARYS}
                    searchParams={{dicTypes: 'WAREHOUSETYPE'}}
                    selectItem={{key: 'dicKey', valueKey: 'dicValue'}}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="行政区"
              >
                {getFieldDecorator('threeCode', {
                  initialValue: modalData.threeCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormCascader
                    dateApi={API_DISTRICE_LIST}
                    searchParams={{type: 2}}
                    size={5}
                    selectItem={{key: 'code', valueKey: 'name'}}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={16}>
              <FormItem
                labelCol={{
                  xs: {span: 24},
                  sm: {span: 4}
                }}
                wrapperCol={{
                  xs: {span: 24},
                  sm: {span: 20}
                }}
                label="地址"
              >
                {getFieldDecorator('warehouseAddress', {
                  initialValue: modalData.warehouseAddress,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="联系人"
              >
                {getFieldDecorator('contactPerson', {
                  initialValue: modalData.contactPerson,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="联系电话"
              >
                {getFieldDecorator('contactPhone', {
                  initialValue: modalData.contactPhone,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="仓库用途"
              // hasFeedback
              >
                {getFieldDecorator('warehousePurpose', {
                  initialValue: modalData.warehousePurpose,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_DICTIONARYS}
                    searchParams={{dicTypes: 'FUNCTION'}}
                    selectItem={{key: 'dicKey', valueKey: 'dicValue'}}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="备注"
                hasFeedback
              >
                {getFieldDecorator('remark', {
                  initialValue: modalData.remark
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="仓库容积(M3)"
                hasFeedback
              >
                {getFieldDecorator('warehouseVolume', {
                  initialValue: modalData.warehouseVolume
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="仓库面积(M2)"
                hasFeedback
              >
                {getFieldDecorator('warehouseArea', {
                  initialValue: modalData.warehouseArea
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
          </Row>
          {/* <h2>配置信息</h2>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="负库存设置"
                hasFeedback
              >
                {getFieldDecorator('negativeStockFlag', {
                  initialValue: modalData.negativeStockFlag,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_WAREHOUSE_WAREHOUSETYPES}
                    searchParams={{type: 'S'}}
                    selectItem={{key: 'key', valueKey: 'value'}}
                  />
                )}
              </FormItem>
            </Col>
          </Row> */}
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



